import {
    Component,
    OnInit,
    ViewEncapsulation
} from '@angular/core';
import { ActivatedRoute, GuardsCheckEnd, NavigationEnd, NavigationStart, Router } from '@angular/router';
import { BaseDirective, detectIE } from '@mckinsey-converge/base';
import {
    ApplicationService,
    BookingsService,
    OrganisationService,
    SpacesService
} from '@mckinsey-converge/data-common';
import { environment } from 'src/environments/environment';
import { MatDialog } from '@angular/material/dialog';
import { ZoomWarningComponent } from '@mckinsey-converge/ui';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: [
        './styles/app.component.scss',
        './styles/custom-element.styles.scss',
        './styles/native-element.styles.scss'
    ],
    encapsulation: ViewEncapsulation.None
})
export class AppComponent extends BaseDirective implements OnInit {
    constructor(
        private _app: ApplicationService,
        private _org: OrganisationService,
        private _spaces: SpacesService,
        private _bookings: BookingsService,
        private _router: Router,
        private _route: ActivatedRoute,
        private _dialog: MatDialog,
    ) {
        super();

        // ensure app is trusted in dev mode so we can preserve login.
        if (!environment.production) {
            localStorage.setItem('trusted', 'true');
        }


        if (detectIE() && detectIE() < 12) {
            location.href = `${location.origin}${location.pathname}assets/not-supported.html`;
            return;
        }
    }

    public ngOnInit(): void {
        this._router.events.subscribe((val: NavigationStart) => {
            const bookingForm = JSON.parse(localStorage.getItem('bookingForm'));
            if (val && val.navigationTrigger && val.url) {
                // Redirect everyone who doesn't have an active bookingForm back to the home page.
                const cleanHash = location?.hash.split('?')[0];
                const isLoadBookingUrl = cleanHash.indexOf('#/home/booking/') === 0;
                const isLoadCateringUrl = cleanHash.indexOf('#/home/catering/') === 0;
                if (!isLoadBookingUrl && !isLoadCateringUrl && cleanHash && ['#/home', '#/home/manage', '#/home/create', '#/home/edit'].indexOf(cleanHash) === -1 && bookingForm && !bookingForm?.activeForm) {
                    location.href = `${location.origin}`;
                    return;
                }
            }

        })
        this.adjustZoom();
    }

    private adjustZoom() {
        const clientWidth = document.body.clientWidth;
        if (this.isMobileOrTablet()) { // Do not show the warning if it's a mobile or tablet device
            return;
        }
        if (clientWidth < 1300) {
            this._dialog.open(ZoomWarningComponent);
        }
    }
    private isMobileOrTablet(): boolean {
        const userAgent = navigator.userAgent || navigator.vendor;
        const maxTouchPoints = navigator.maxTouchPoints || 0; // Check for touch points 
        if (maxTouchPoints > 0 && window.innerWidth <= 1024) {
            return true;
        } // Check for common mobile/tablet user agent strings 
        const mobileAgents = ['Android', 'webOS', 'iPhone', 'iPad', 'iPod', 'BlackBerry', 'IEMobile', 'Opera Mini'];
        for (const agent of mobileAgents) {
            if (userAgent.indexOf(agent) > -1) {
                return true;
            }
        }
        return false;
    }
}
