import {
    Component,
    Directive,
    ElementRef,
    HostListener,
    Input,
    OnInit
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import {
    select,
    Store
} from '@ngrx/store';
import {
    BaseDirective,
} from '@mckinsey-converge/base';
import {
    User,
    Booking,
    Building,
    BuildingCity,
    flattenDisplayOffices,
    selectCancelledBookingsResultsFromState,
    selectUpcomingBookingsResultsFromState,
    selectPastBookingsResultsFromState,
    selectBookingByIdResultsFromState,
    UsersService,
} from '@mckinsey-converge/data-common';
import {
    clearBookingByIdResults,
} from '@mckinsey-converge/bookings';
import {
    BehaviorSubject,
} from 'rxjs';
import {
    distinctUntilChanged,
    first,
    map,
    take,
} from 'rxjs/operators';
import { loadBookingsWithQuery } from '@mckinsey-converge/bookings';
import { loadOrganisations } from '@mckinsey-converge/organisation';
import {
    SelectOption
} from '@mckinsey-converge/ui';
import {
    buildingStateSelector,
    selectBuildingOptionsGroupedByCity
} from '@mckinsey-converge/buildings';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { BookingDetailsModalComponent } from '../booking-details-modal/booking-details-modal.component';
import { selectCurrentUser } from '@mckinsey-converge/user';
import { RoomBookingStoreState } from '../room-booking/room-booking.component';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { SearchBarConfig } from 'libs/legacy-ui/src/lib/cng-search-bar/cng-search-bar.component';


@Component({
    selector: 'app-manage-bookings',
    template: `
        <div class="mck-column mck-content-expand mck-content-page-container manage-booking">
            <div *ngIf="hasLoaderUrl" class="modal_mask">&nbsp;</div>
            <header class="header">
                <a-topbar-header [mode]="TopBarHeaderMode.Light"></a-topbar-header>
            </header>
            <div class="mck-column">
                <div class="bookings-header">
                    <div class="inner-container">
                        <h1 >Manage Bookings</h1>

                        <div [class.desktop-only]="true" class="manage-booking-filters" >
                            
                            <cng-search-bar [config]="searchBarConfig" (search)="searchBooking($event)" ></cng-search-bar>
                            <div class="delegates" *ngIf="has_delegate" >
                                <cng-select [data]="delegateDD" *ngIf=" hostOptions && hostOptions.length"></cng-select>
                            </div>
                        
                        </div>


                        <mat-tab-group class="mck-tab" (selectedTabChange)="clearPage($event)">
                        
                            <div *ngFor="let type of bookingTypes;">
                                <mat-tab [label]="type.display">
                                    <app-bookings-listing [activeTab]="activeTab" [config]="type" [buildings]="buildings" [searchBookingBy]="searchBookingBy" [selectedHost]="selectedHost"></app-bookings-listing>
                                </mat-tab>
                            </div>
                        </mat-tab-group>

                    </div>
                </div>
            </div>
        </div>
    `,
    styleUrls: ['./manage-bookings.component.scss']
})
export class ManageBookingsComponent extends BaseDirective implements OnInit {
    public TopBarHeaderMode = TopBarHeaderMode;
    public buildings = new BehaviorSubject<SelectOption<any>[]>([]);
    public bookingTypes = [
        {
            type: 'upcoming',
            display: 'Upcoming',
            selector: selectUpcomingBookingsResultsFromState
        },
        {
            type: 'past',
            display: 'Past',
            selector: selectPastBookingsResultsFromState
        },
        {
            type: 'cancelled',
            display: 'Cancelled',
            reverse_sort: true,
            selector: selectCancelledBookingsResultsFromState
        },
    ];

    /** Current User */
    public currentUser: User;
    public dialogRef: MatDialogRef<BookingDetailsModalComponent, any>;
    public modalInitialized = false;
    public hasLoaderUrl = true;

    public activeTab = 'upcoming';

    /**
     * Configs for delegate dropdown
     */
    /* if user has no delegates, hide the dropdown */
    public has_delegate: boolean = false; 
    /* Holds list of all host/delegates */
    public host_list: User[];
    public hostOptions: { display: string; value: string; meta: { user: User; }; }[];
    public delegateDD: { ddBoxClass: string; options: any; defaultSelected: any; onChangeCallback: any; ddOptionBoxWidth: string };
    public selectedHost: string = '';

    public searchBookingBy: any = '';


    searchBarConfig: SearchBarConfig = {
        placeholder: 'Search by title, room name, attendees...',
        totalFoundItem: 0,
        showSrchWarning: false
      };

    constructor(
        private dialog: MatDialog,
        private store: Store<RoomBookingStoreState>,
        private route: ActivatedRoute,
        private _router: Router,
        private _users: UsersService,
    ) {
        super();
    }

    public ngOnInit(): void {

        // Get the router params
        const bookingId = this.route.snapshot.paramMap.get('id');
        const verb = this.route.snapshot.paramMap.get('verb');
        // Load the buildings data
        this.store.dispatch(loadOrganisations.request());
        this.route.data.subscribe(initData => {
            // Determine if the cancel or booking submodal is loading
            const cancel = verb === 'cancel';
            const addCatering = initData.hasCatering;
            /**
             * When using the catering or cancel URLs,
             * Users reported the modals loading after one another was
             * jarring so, so the UI is masked out until the modal is loaded.
             */
            this.hasLoaderUrl = addCatering || cancel;

            this.subscription('buildings', this.store.pipe(
                select(buildingStateSelector),
                select(selectBuildingOptionsGroupedByCity),
                map((grouped: Map<BuildingCity, Building[]>) => {
                    return flattenDisplayOffices(grouped);
                }),
                distinctUntilChanged()
            ).subscribe(next => {
                this.buildings.next(next);
                    if (bookingId && !this.modalInitialized) {
                        this.store.dispatch(clearBookingByIdResults())
                        this.modalInitialized = true;
                        // Once the data loads viewBooking
                        this.subscription('bookingResults', this.store.pipe(
                            select(selectBookingByIdResultsFromState)
                        ).subscribe(next => {
                            const booking = next.success ? next.success?.[0] : null;
                            if (booking && booking.room) {
                                // Modal Madness, close the existing booking details modals.
                                if (this.dialogRef) {
                                    this.dialogRef.close();
                                }
                                if (this.dialog) {
                                   this.dialog.closeAll();
                                }
                                // Call viewBookingFromEmailLink with cancel = true to trigger the cancel modal
                                // Call viewBookingFromEmailLink with addCatering = true to trigger the add Catering modal
                                this.viewBookingFromEmailLink(booking, cancel, addCatering);

                                // Remove the "loader" url from view
                                this.timeout('rewrite url',() => {
                                    this._router.navigate(['/home','manage'], { replaceUrl: true });
                                })
                            }
                        }));
                    }
            }));

        })

        this.subscription('currentUser', this.store.select(selectCurrentUser)
            .pipe(take(1))
            .subscribe((user: User | undefined) => {
                // Once we know we have the user loaded.
                this.currentUser = new User(user);
                if (bookingId) {
                    // If there's a booking ID loadBookingById
                    this.store.dispatch(loadBookingsWithQuery({ 'id': bookingId, 'email': this.currentUser.email, 'target': 'bookingById', 'offset': 0 }));
                }
            }));


            this._users.initialised.pipe(first((_) => _)).subscribe(() => {
                const user: User = this._users.current;

                const delegates = user.delegates.map((del) => {
                    const ifUser = this._users.find(del.email);
                    return ifUser ? 
                        new User({ email: ifUser.email, name: ifUser.name, opt_out: del.opt_out }) : 
                        new User({ email: del.email, name: del.email.split('@')[0].split('_').map((s) => s.charAt(0).toUpperCase() + s.substring(1))
                        .join(' ')
                        , opt_out: del.opt_out });
                    });

                if (delegates.length) {
                    this.has_delegate = true;

                    this.host_list = [user].concat(delegates);
                    const options = this.host_list.map(h => ({ display: h.name, value: h.email, meta: { user: h } }))
                    
                    this.hostOptions =  [{ display: 'All', value: '', meta: { user: null } }].concat(options);
                    this.selectedHost = this.hostOptions[0].value;
                    this.delegateDD = {
                        ddBoxClass: '',
                        options: this.hostOptions,
                        defaultSelected: this.hostOptions[0],
                        onChangeCallback: this.onChangeDelegates.bind(this),
                        ddOptionBoxWidth: '200px'
                    }
                }
                
            });
    }

    public onChangeDelegates(del) {
        this.selectedHost = del.value;
    }

    public getBuilding(booking: Booking) {
        if (this.buildings && booking.building_zone) {
            return this.buildings.value.find(b => booking.building_zone === b.value)?.meta.building;
        }
        return null;
    }

    // Only fires from an email link
    public viewBookingFromEmailLink(booking: Booking, cancel: boolean = false, addCatering: boolean = false) {
        if (this.modalInitialized && this.dialogRef) {
            this.dialogRef.close();
            this.dialogRef = null;
        }
        // R--- Verify the email links are working.
        // Open the booking details modal
        if (!this.dialogRef && booking.room) {
            this.dialogRef = this.dialog.open(BookingDetailsModalComponent, {
                panelClass: 'mck-modal-wide-manage-booking',
                data: {
                    booking,
                    building: this.getBuilding(booking),
                    cancel,
                    addCatering,
                    hasLoaderUrl: this.hasLoaderUrl,
                    refresh: () => null
                },
                autoFocus: false,
                backdropClass: 'mck-modal-backdrop',
            });
            // Close up the loader panel
            setTimeout(() => {
                this.hasLoaderUrl = false;
            }, 1000);
        }
    }

    public clearPage(event : MatTabChangeEvent) {
        this.activeTab = this.bookingTypes[event.index].type;
        this._router.navigate([], { relativeTo: this.route, queryParams: { page: 1 } });
    }


    searchBooking(value) {
        this.searchBookingBy = value;
    }

}
