import { Component, Input, OnInit, OnChanges, ChangeDetectionStrategy, ChangeDetectorRef, NgZone } from '@angular/core';
import { throttleTime, tap, first, distinctUntilChanged, map } from 'rxjs/operators';
import { fromEvent } from 'rxjs';
import { TopBarHeaderMode } from 'src/app/components/topbar-header/topbar-header.component';
import { select, Store } from '@ngrx/store';
import { BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';
import { BaseDirective } from '@mckinsey-converge/base';
import { BuildingStoreState, buildingStateSelector, selectBuildingOptionsGroupedByCity } from '@mckinsey-converge/buildings';
import { MatDialog } from '@angular/material/dialog';
import { Booking, User, IEngineResponse, BookingStoreState, clearBookingFormData, BookingsService, UsersService, flattenDisplayOffices, BuildingCity, Building } from '@mckinsey-converge/data-common';
import { UserStoreState } from '@mckinsey-converge/user';
import { loadBookingsWithQuery, loadUpcomingBookingsResults, MyBookingsStoreState } from '@mckinsey-converge/bookings';
import { LoadingModel } from '@mckinsey-converge/loading';
import { SelectOption } from '@mckinsey-converge/ui';
import {
    mapResultsToViewModels,
    BookingResultsViewModel,
} from 'src/app/booking/manage-bookings/manage-bookings.viewmodel';
import { selectCurrentUser } from '@mckinsey-converge/user';
import { BookingDetailsModalComponent } from '../booking-details-modal/booking-details-modal.component';
import { BookingPagination } from '@mckinsey-converge/bookings';
import { DateNow } from '@mckinsey-converge/date-tz';
import { ActivatedRoute, Router } from '@angular/router';
import { loadRoomsForResults } from '@mckinsey-converge/rooms';

export type ManageBookingsStoreState = BookingStoreState &
    BuildingStoreState &
    MyBookingsStoreState &
    UserStoreState;

@Component({
    selector: 'app-bookings-listing',
    templateUrl: './bookings-listing.component.html',
    styleUrls: ['./bookings-listing.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class BookingsListingComponent extends BaseDirective implements OnInit, OnChanges {
    @Input() public config: any;
    @Input() public buildings: BehaviorSubject<SelectOption<any>[]>;
    @Input() public searchBookingBy: string;
    @Input() public selectedHost: string;
    @Input() public activeTab: string;

    public bookingResults = new BehaviorSubject<LoadingModel<IEngineResponse>>(
        LoadingModel.empty()
    );
    public bookings: BookingResultsViewModel[];
    public showBookings = false;
    public reloading = false;
    public TopBarHeaderMode = TopBarHeaderMode;
    public mobile: boolean = false;

    /** Current User */
    public currentUser: User;
    master_booking: BookingResultsViewModel;
    allBookings: any[];

    public pagination: BookingPagination = {
        currentPage: 1,
        totalRecords: 0,
        pageLimit: 10,
    };
    bookingPerPage: any[] = [];
    filteredByHost: any[] = [];

    public get lastPage() {
        return this.pages.total === this.pagination.currentPage;
    }

    public get pages() {
        const p = this.pagination;
        const total = Math.ceil(p.totalRecords / p.pageLimit) ;
        const group = Math.ceil(p.currentPage / 5);
        const startPosition = Math.max(
            1,
            Math.min(group > 1 ? (group - 1) * (p.pageLimit + 1)  : 1, total - 4)
        );
        let pages = [];
        for (let x = startPosition; x <= Math.min(startPosition + 4, total); x++) {
            pages.push(x);
        }
        if (!pages.length) {
            pages.push(1);
        }
        const maxed = () => {
            if (total <= 5 || p.currentPage >= total) {
                return false;
            }
            return true;
        };
        const mined = () => {
            if (total <= 5 || p.currentPage < 6) {
                return false;
            }
            return true;
        };
        const start = p.currentPage > 5;
        const end = !(p.currentPage >= total);
        return {
            total,
            pages,
            min: mined(),
            max: maxed(),
            start,
            end,
        };
    }

    constructor(
        private store: Store<ManageBookingsStoreState>,
        private _route: ActivatedRoute,
        private _router: Router,
        private dialog: MatDialog,
        private _bookings: BookingsService,
        private _zone: NgZone,
        private cdRef: ChangeDetectorRef
    ) {
        super();
    }

    public ngOnInit(): void {
        const breakpoint = 450;
        const selector = this.config.selector;
        const target = this.config.type;

        this.subscription(
            'currentUser',
            this.store
                .select(selectCurrentUser)
                .pipe(take(1))
                .subscribe((user: User | undefined) => {
                    // Once we know we have the user loaded.
                    this.currentUser = new User(user);
                   
                })
        );

        this.subscription('buildings', this.store.pipe(
            select(buildingStateSelector),
            select(selectBuildingOptionsGroupedByCity),
            map((grouped: Map<BuildingCity, Building[]>) => {
                return flattenDisplayOffices(grouped);
            }),
            distinctUntilChanged()
        ).subscribe(next => {
            // this.buildings.next(next);
            if(this.activeTab === this.config.type) {
                this.store.dispatch(
                    loadBookingsWithQuery({
                        email: this.currentUser.email,
                        from: DateNow(new Date()).ms,
                        target,
                        offset: 0,
                    })
                );
            }

            // start the timer at the top of the minute;
            const offset = DateNow(new Date()).startOfValue('minute').addValue({ minutes: 1 }).ms - DateNow(new Date()).ms;
            // fetch it on one minute interval now that there is room data.
            setTimeout(() => {
                this.refresh(target);
                this._zone.runOutsideAngular(() => {
                    this.interval('refreshBookings', () => this.refresh(target, true), 60 * 1000);
                });

            }, offset + 5000);

               
        }));



        const sort_asc = (a, b) => a.date - b.date;
        const sort_desc = (a, b) => b.date - a.date;

        this.subscription(
            'bookingResults',
            this.store.pipe(select(selector)).subscribe((next) => {
                this.bookingResults.next(next);
                if (!next.isEmpty() && Array.isArray(next?.success?.results)) {
                    let results = [...next?.success?.results].sort(
                        this.config.reverse_sort ? sort_desc : sort_asc
                    );
                    this.showBookings = !!results.length;
                    this.reloading = false;
                    // save all bookings to reuse later 
                    this.allBookings = results;
                    
                    /** Filter all booings of active tab by selected host if any  */
                    this.filterBySelectedHost(true);

                } else {
                    this.showBookings = false;
                    this.reloading = false;
                    this.bookings = [];
                    this.pagination.totalRecords = 0;
                    this.cdRef.detectChanges();
                }
            })
        );

        if (window) {
            this.mobile = window.innerWidth < breakpoint;

        }

        fromEvent(window, 'resize')
            .pipe(throttleTime(500))
            .pipe(
                tap({
                    next: (val) => {
                        const target = val.target as Window;
                        this.mobile = target.innerWidth < breakpoint;
                    },
                })
            ).subscribe();
            
        this.timeout('query', () =>
            this.subscription(
                'route.query',
                this._route.queryParamMap.subscribe((params) => {
                    if (params.has('page')) this.setPage(+params.get('page'), false);
                })
            )
        );
    }

    //For each result from results, it find its level and building from the lists and map with BookingResultsViewModel but filter all master from list.
    public getViewModel = (results) => mapResultsToViewModels(results).filter(b => !(b.booking.is_recurrent && b.booking.recurrence_type === 'master'));


    public splitArrayIntoSubarrays(originalArray, subarrayLength) {
        var newArray = [];
        for (var i = 0; i < originalArray.length; i += subarrayLength) {
            newArray.push(originalArray.slice(i, i + subarrayLength));
        }
        return newArray;
    }


    public ngOnChanges($change: any) {
        
        if ($change?.buildings?.currentValue !== $change?.buildings?.previousValue || $change?.activeTab?.currentValue !== $change?.activeTab?.previousValue) {
            this.refresh(this.config.type);
        }
        if ($change?.selectedHost?.currentValue !== $change?.selectedHost?.previousValue) {
            this.filterBySelectedHost(true);
        } else
            if ($change?.searchBookingBy?.currentValue !== $change?.searchBookingBy?.previousValue) {
                this.filterBySelectedHost(false);
            }
    }



    public filterBySelectedHost(isHostChanged: boolean) {

        // Converting search value to lower case for case-insensitive comparison
        const searchHost =  this.selectedHost.toLowerCase().trim();
        // this.filteredByHost = [];

        if(this.allBookings) {

            if(searchHost) {
                if(isHostChanged) {
                    // Combining event list and space arrays into one arrya for searchig 
                    const allItems = [...this.allBookings];
                    // Filtering the combine array to find matching host email 
                    this.filteredByHost = allItems.filter( (item: any ) => 
                        this.isIncludes(item?.host, 'email', searchHost) ||
                        this.isIncludes(item?.organiser, 'email', searchHost)
                    );
                }
                else {
                    this.filteredByHost =  [...this.filteredByHost];
                }
                
            }
             else {
                this.filteredByHost =  [...this.allBookings];
             }

             //Now send filtered booking by host to filter further by search input given in the field
             this.filterBookingsBySearchValue([...this.filteredByHost]);

            
        }
    }

    
    public isIncludes = (item: any, field: string, searchVaue: string) => (item && item[field] && item[field].toLowerCase().includes(searchVaue));

    public filterBookingsBySearchValue(filteredByHost) {

        // Converting search value to lower case for case-insensitive comparison
        const searchValue = this.searchBookingBy.toLowerCase().trim();
        let filteredItems = [];

        if(filteredByHost) {

            if(searchValue) {
                // Combining event list and space arrays into one arrya for searchig 
                const allItems = [...filteredByHost];
                // Filtering the combine array to find matching room or booking 
                filteredItems = allItems.filter( (item: any ) => 
                this.isIncludes(item, 'title', searchValue) ||
                this.isIncludes(item?.creator, 'name', searchValue) ||
                this.isIncludes(item?.organiser, 'name', searchValue) ||
                this.isIncludes(item, 'local_room_name', searchValue) || // search with room name 
                this.isIncludes(item, 'simple_name', searchValue) || 
                (item?.attendees && !!item?.attendees.filter(el => el.name.toLowerCase().includes(searchValue)).length ) 
                );
            }
             else {
                filteredItems =  [...filteredByHost];
             }

             const filteredBookings = this.getViewModel(filteredItems);
            
             this.pagination.totalRecords = filteredBookings.length;
             this.pagination.currentPage = !this.lastPage ? 1 : this.pagination.currentPage;
             this.bookingPerPage = this.splitArrayIntoSubarrays(filteredBookings, this.pagination.pageLimit);
             this.bookings = this.bookingPerPage[this.pagination.currentPage-1];
             this.cdRef.detectChanges();
        }
    
    }

   




    public showResults() {
        // Don't show the loading state while we're reloading with Building details.
        return this.reloading; // || !this.bookingResults?.value?.isLoading;
    }

    public getBuilding(booking: Booking) {
        if (this.buildings && booking.building_zone) {
            return this.buildings.value.find((b) => booking.building_zone === b.value)?.meta.building;
        }
        return null;
    }

    // From the Manage Bookings page
    public async viewBooking(viewModel: BookingResultsViewModel) {

        if(viewModel.booking.is_recurrent) {
            let booking = this.allBookings.find(bkn => bkn.id === viewModel.booking.recurrence_master_id && bkn.recurrence_type === 'master1') ;
            if (!booking) {
                booking = await this._bookings.show(viewModel.booking.recurrence_master_id);
            }
            this.master_booking = mapResultsToViewModels([booking])[0];
        }
        this.cdRef.detectChanges();

        // real booking.room here.
        const dialogRefX = this.dialog.open(BookingDetailsModalComponent, {
            panelClass: 'mck-modal-wide-manage-booking',
            data: {
                booking: viewModel.booking,
                refresh: () => {
                    this.refresh('all');
                },

                building: this.getBuilding(viewModel.booking),
                master_booking: this.master_booking?.booking || null,
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });

        dialogRefX.afterClosed().subscribe((flag) => {
            flag !== 'DoNotClearActiveForm' && this.clearFormAndResultData();
            this.cdRef.detectChanges();
            // this.timeout('refreshBookings', () => this.refresh('all'), 1000);
            // this.timeout('refreshBookings', () => this.refresh('all'), 5000);
        });
    }

    public clearFormAndResultData() {
        this.store.dispatch(clearBookingFormData());
        this.store.dispatch(loadRoomsForResults.clear());
        this.cdRef.detectChanges();
    }

    public readonly prevPage = () => this.setPage(this.pagination.currentPage - 1);
    public readonly nextPage = () => this.setPage(this.pagination.currentPage + 1);
    public readonly prevGroup = () => this.setPage(this.pages.pages.length);
    public readonly nextGroup = () => this.setPage(this.pages.pages.length + 1);
    public readonly gotoPage = (page: number) => this.setPage(page);

    public setPage(page_idx: number, route: boolean = true) {
        if (!this.pages.total) return;
        const page = Math.max(1, Math.min(page_idx, this.pages.total));
        if (route) this._router.navigate([], { relativeTo: this._route, queryParams: { page } });
        this.pagination.currentPage = page;
        this.bookings = this.bookingPerPage[this.pagination.currentPage-1];
        this.cdRef.detectChanges();
        // this.refresh();
    }

    public refresh(target: string = this.activeTab, dontShowReloading = false) {
        if (!this.currentUser?.email) {
            return;
        }

        
        const offset = 0 ; //(this.pagination.currentPage - 1) * this.pagination.pageLimit;
        // fetch it on one minute interval now that there is room data.
        if(this.activeTab === this.config.type) {

             this.store.dispatch(
            loadBookingsWithQuery({
                    email: this.currentUser.email,
                    from: DateNow(new Date()).ms,
                    target:this.activeTab,
                    offset,
                })
            );
            this.reloading = !dontShowReloading;
            this.cdRef.detectChanges();
        }
       

       

        
    }

    identify(index, item) {
        return item.booking.id;
    }
}
